.container {
	position: relative;
}
/* header */
header {
	display: flex;
	justify-content: space-between;
	align-items: center;

	width: 100%;
	height: 80px;
	background: black;
}
header .logo {
	background-image: url(./../images/character_licensing/logo.png);
	width: 146px;
	height: 60px;
	margin-left: 240px;
}
header .exit_button {
	width: 48px;
	margin-right: 388px;
}

/* home */
.home.content-container {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.home__banner {
	width: 100%;
	height: 564px;
	background: url(./../images/character_licensing/banner.jpg);
}

.home__subtitle {
	margin-top: 100px;
	margin-bottom: 30px;
	font-family: 'Noto Sans KR';
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 160%;
	/* or 38px */

	letter-spacing: -0.015em;

	color: #212529;
}
.home__title {
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 700;
	font-size: 32px;
	line-height: 160%;
	/* or 51px */

	letter-spacing: -0.015em;

	color: #113a71;
}
.home__text-wrapper {
	margin-top: 50px;
	margin-bottom: 200px;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 1440px;
	height: 184px;
	background: #eaeaea;
}
.home__text {
	font-family: 'Noto Sans KR';
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 180%;
	/* or 29px */

	text-align: center;
	letter-spacing: -0.015em;

	color: #212529;
}

/* character video */
.character-video.content-container {
	width: 1440px;
	margin: auto;
}
.character-video__title {
	margin-bottom: 50px;
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 700;
	font-size: 32px;
	line-height: 160%;
	/* or 51px */

	letter-spacing: -0.015em;

	color: #000000;
}
.character-video__main-video {
	display: block;
	margin: auto;
	width: 1381px;
	height: 800px;
	margin-bottom: 50px;
}
.character-video__border {
	border-bottom: 1px solid #96c6eb;
	width: 1440px;
	margin-bottom: 50px;
}
.character-video__horizon-cards-wrapper {
	display: flex;
	gap: 70px;
	flex-direction: column;
}

